'use client';

import Link from 'next/link';
import Image from 'next/image';
import { usePathname } from 'next/navigation';
import { useState } from 'react'; // Added for mobile menu toggle

export default function Navbar() {
  const pathname = usePathname();
  const [isMenuOpen, setIsMenuOpen] = useState(false); // State for mobile menu toggle

  // Only show navbar on home page
  if (pathname !== '/') {
    return null;
  }

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
    setIsMenuOpen(false); // Close mobile menu after scrolling
  };

  return (
    <header className="sticky top-0 z-50 bg-white shadow-lg">
      <div className="container mx-auto px-4 py-1 flex justify-between items-center">
        {/* Logo - Increased size to 80x80 with padding */}
        <Link href="/" className="flex items-center space-x-2">
          <Image
            src="/images/logo.png"
            alt="TripMitra Logo"
            width={100}
            height={100}
            className="rounded-lg p-2" // Added padding for better alignment
          />
          <span className="text-xl font-bold text-black">TripMitra</span>
        </Link>

        {/* Navigation Links - Scroll to sections */}
        <nav className="hidden md:flex space-x-8">
          <button
            onClick={() => scrollToSection('destinations')} // Fixed: scroll to 'destinations' instead of 'features'
            className="font-medium text-gray-700 hover:text-blue-600 transition-colors"
          >
            Destinations
          </button>
          <button
            onClick={() => scrollToSection('pricing')}
            className="font-medium text-gray-700 hover:text-blue-600 transition-colors"
          >
            Pricing
          </button>
          <button
            onClick={() => scrollToSection('contact')}
            className="font-medium text-gray-700 hover:text-blue-600 transition-colors"
          >
            Contact
          </button>
        </nav>

        {/* Mobile menu toggle button */}
        <button
          onClick={() => setIsMenuOpen(!isMenuOpen)}
          className="md:hidden text-gray-700 focus:outline-none"
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d={isMenuOpen ? "M6 18L18 6M6 6l12 12" : "M4 6h16M4 12h16m-7 6h7"} />
          </svg>
        </button>
      </div>

      {/* Mobile Navigation Menu */}
      {isMenuOpen && (
        <div className="md:hidden bg-white shadow-lg">
          <nav className="flex flex-col space-y-4 px-4 py-4">
            <button
              onClick={() => scrollToSection('destinations')}
              className="font-medium text-gray-700 hover:text-blue-600 transition-colors text-left"
            >
              Destinations
            </button>
            <button
              onClick={() => scrollToSection('pricing')}
              className="font-medium text-gray-700 hover:text-blue-600 transition-colors text-left"
            >
              Pricing
            </button>
            <button
              onClick={() => scrollToSection('contact')}
              className="font-medium text-gray-700 hover:text-blue-600 transition-colors text-left"
            >
              Contact
            </button>
          </nav>
        </div>
      )}
    </header>
  );
}
